.card {
    background: #fff;
    border-radius: 2px;
    height: 170px;
    margin: 1rem;
    position: relative;
    width: 300px;
    -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
            box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
    float: left;
    -o-object-fit: cover;
       object-fit: cover;
  }
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
    /* background:#fff; */
  }
  .card > .img-container > img {
    width: 100%
  }
   
  


